export default class PostsContainer extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      'posts': null,
      'hasMore': false,
    }
  }

  _postObjectToComponent = (obj) => (
    <Post key={ randomString() } {...obj} />
  )

  componentWillMount() {
    this.setState({
      posts: postsList.map(post => this._postObjectToComponent(post)),
      hasMore: true,
    })
  }

  loadMore = (pageNum, callback = undefined) => {
    setTimeout(() => {
      this.setState({
        posts: [
          ...this.state.posts,
          postsList.map(
            post => this._postObjectToComponent(post)
          )
        ],
        hasMore: true
      }, () => {
        if (callback !== undefined) {
          callback()
        }
      })
    }, 500)
  }

  render() {
    return (
      <div className="posts">
        <InfinityScroll
            pageStart={1}
            bottomOffset={250}
            loadMore={this.loadMore}
            hasMore={this.state.hasMore}
            loader={<div className="loader">Loading ...</div>}
          >
            {this.state.posts}
        </InfinityScroll>
      </div>
    )
  }
}



