import React from 'react'
import './Post.css'

export default class Post extends React.PureComponent {
  render() {
    return (
      <div className="post">
        <h4>{this.props.title}</h4>
        <p>{this.props.text}</p>
        <p className="date">{this.props.date}</p>
      </div>
    )
  }
}



