import React from 'react'
import logo from './logo.svg'
import './App.css'

import PostsContainer from './containers/PostsContainer'

class App extends React.PureComponent {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>react-infinity-scroll</h2>
        </div>
        <div className="App-intro">
          <p>
            Welcome to the demo mini-site for my little package <code>react-infinity-scroll</code>
          </p>
          <p>
            Here you can scroll for the whole day to feel how this stuff works. For your info, I don't use any API to simplify the example. So all the posts are the same 10.
          </p>
          <h3>Here come infinity posts:</h3>
        </div>
        <PostsContainer />
      </div>
    );
  }
}

export default App;



